* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    font-size: 62.5%;
    overflow-x: hidden;
}

body {
    font-family: "Lato", sans-serif;
    line-height: 1;
    font-weight: 700;
    color: #555;
}

/* HomePage */

.container {
    max-width: 120rem;
    margin: 0 auto;
}

.grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 3.2rem;
    margin: 9.6rem 0;
    padding: 0 16rem;
}

.card {
    /* Add shadows to create the "card" effect */
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2);
    transition: 0.3s;
    border-radius: 4px;
}

/* On mouse-over, add a deeper shadow */
.card:hover {
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
    cursor: pointer;
}

.card:hover .card__img_description {
    visibility: visible;
    opacity: 1;
    background: rgba(0, 0, 0, 0.6);
}

.card__img_wrapper {
    position: relative;
    height: 18rem;
}

.card__img_description {
    color: #fff;
    visibility: hidden;
    opacity: 0;
    top: 0;
    width: 100%;
    height: 100%;
    position: absolute;
    text-align: center;
    /* transition effect. not necessary */
    transition: opacity 0.2s, visibility 0.2s;
}

.card__img {
    object-fit: cover;
    width: 100%;
    height: 100%;
}

.learn-more {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

.card__meta {
    padding: 2.4rem;
}

.card__content {
    margin-top: 1.2rem;
    display: flex;
    flex-direction: column;
    gap: 2.4rem;
}

.card__heading {
    font-weight: 700;
    color: #000;
}

.card__description {
    font-weight: 500;
    letter-spacing: 0.5px;
    line-height: 1.4;
    color: #999;
}

.card__footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #777;
    font-weight: 500;
    letter-spacing: 0.5px;
}

.circle-container {
    display: flex;
    align-items: center;
    gap: 0.5rem;
}

.circle-container .circle {
    width: 1.2rem;
    height: 1.2rem;
    border-radius: 50%;
}

.bluebg {
    background-color: #04bcd4;
}

.yellowbg {
    background-color: #fcc50e;
}

.modal {
    display: none;
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgb(0, 0, 0);
    background-color: rgba(0, 0, 0, 0.8);
    visibility: hidden;
    animation: fadeout 0.5s;
    overflow: auto;
}

.modal-visible {
    visibility: visible;
    display: block;
    animation: fadein 0.5s;
}

@keyframes fadein {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

.modal__content {
    background-color: #fefefe;
    left: 50%;
    top: 50%;
    position: absolute;
    transform: translate(-50%, -50%);
    max-height: 90vh;
    border: 1px solid #888;
    width: 30%;
    border-radius: 5px;
    overflow: auto;
}

.modal__action {
    display: flex;
    justify-content: flex-end;
    padding: 1.2rem 1.8rem;
}

.modal__close_btn {
    height: 2.4rem;
    width: 2.4rem;
    cursor: pointer;
    transition: all 0.2s ease;
}

.modal__close_btn:hover {
    transform: scale(1.05);
}

.modal__img {
    width: 100%;
}

.modal__body {
    padding: 2.4rem;
    display: flex;
    flex-direction: column;
    gap: 2.4rem;
    justify-content: space-between;
}

.modal__footer {
    display: flex;
    align-items: center;
    gap: 1.2rem;
}

.modal__author_image_box {
    width: 2.4rem;
    height: 2.4rem;
    overflow: hidden;
}

.modal__avatar {
    object-fit: cover;
    width: 4.8rem;
    height: 4.8rem;
    vertical-align: middle;
    border-radius: 50%;
}

.modal__avatar_text {
    text-align: center;
    border-radius: 50%;
    width: 4.8rem;
    height: 4.8rem;
    background-color: #fcc50e;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #000;
    font-size: 2.4rem;
}

.modal__body_title {
    color: #111;
    letter-spacing: 0.05px;
    font-weight: 800;
}

.modal__body_description {
    font-weight: 500;
    letter-spacing: 0.1px;
    line-height: 1.4;
    color: #999;
}

.modal__body_description {
    font-weight: 500;
    letter-spacing: 0.1px;
    line-height: 1.4;
    color: #999;
}
.modal__author {
    color: #777;
    font-weight: 500;
    font-size: 1.2rem;
}

@media (max-width: 75em) {
    html {
        font-size: 56.25%;
    }
    .grid {
        padding: 0 12rem;
    }
    .modal__content {
        width: 40%;
    }
}

@media (max-width: 59em) {
    html {
        font-size: 50%;
    }

    .grid {
        padding: 0 9.6rem;
    }

    .modal__content {
        width: 45%;
        border-radius: 5px;
    }

    .card__heading {
        font-size: 1.8rem;
    }

    .card:hover .card__img_description {
        visibility: hidden;
        opacity: 0;
    }
}

@media (max-width: 44em) {
    .grid {
        padding: 0 16rem;
        grid-template-columns: 1fr;
    }
    .modal__content {
        width: 60%;
    }
    .modal__avatar {
        width: 3.6rem;
        height: 3.6rem;
    }

    .modal__avatar_text {
        width: 3.6rem;
        height: 3.6rem;
        font-size: 1.8rem;
    }
}

@media (max-width: 34em) {
    .grid {
        padding: 0 8.4rem;
        grid-template-columns: 1fr;
    }
    .modal__avatar {
        width: 2.8rem;
        height: 2.8rem;
    }

    .modal__avatar_text {
        width: 2.8rem;
        height: 2.8rem;
        font-size: 1.4rem;
    }
    .modal__content {
        width: 70%;
    }
}
